.shop-by-category {
    margin: 25px 0;
    @include md {
      margin: 50px 0;
    }
  
    .categories {
      display: flex;
      flex-flow: wrap;
      gap: 10px;
  
      .category {
        width: calc(50% - 5px);
        @include md {
          width: calc(35% - 10px);
        }
  
        .category-card {
          background-color: black;
          cursor: pointer;
          overflow: hidden;
          position: relative;
  
          img {
            width: 100%;
            display: block;
            transition: all ease 0.3s;
          }
  
          .category-details {
            position: absolute;
            bottom: 0;
            left: 0;
            right: 0;
            padding: 10px;
            background-color: rgba(0, 0, 0, 0.6);
            color: white;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            opacity: 0;
            transition: all ease 0.3s;
          }
  
          &:hover {
            img {
              transform: scale(1.2);
            }
  
            .category-details {
              opacity: 1;
            }
          }
        }
  
        .category-name {
          font-weight: 500;
          text-transform: uppercase;
          margin-bottom: 10px;
          font-size: 14px;
        }
  
        .category-button {
          background-color: #9b4f7c;
          color: white;
          padding: 10px;
          border: none;
          border-radius: 4px;
          cursor: pointer;
          transition: all ease 0.3s;
  
          &:hover {
            background-color: #b56c95;
          }
        }
      }
    }
  
    .sec-heading {
      margin-bottom: 20px;
      font-size: 18px;
      font-weight: 500;
      text-transform: uppercase;
      @include md {
        margin-bottom: 35px;
        font-size: 24px;
      }
  
      &:after {
        content: "";
        display: block;
        margin-top: 5px;
        width: 50px;
        height: 3px;
        background-color: #9b4f7c;
        @include md {
          margin-top: 10px;
        }
      }
    }
  }
  

  .product-categories-card {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: space-between;
	padding: 2rem;
	border-radius: 1rem;
	transition: background-color 0.3s ease-in-out;
	cursor: pointer;
	width: 100%;
	max-width: 300px;
  }
  
  .product-categories-card:hover {
	background-color: #f7f7f7;
  }
  
  .product-categories-card__image-container {
	width: 100%;
	height: 200px;
	display: flex;
	align-items: center;
	justify-content: center;
  }
  
  .product-categories-card__image-container img {
	max-width: 100%;
	max-height: 100%;
	object-fit: contain;
  }
  
  .product-categories-card__content {
	display: flex;
	flex-direction: column;
	align-items: center;
	width: 100%;
  }
  
  .product-categories-card__title {
	margin: 1rem 0;
	font-size: 1.5rem;
	font-weight: bold;
	text-align: center;
  }
  
  .product-categories-card__button {
	padding: 0.5rem 1rem;
	background-color: #222;
	color: #fff;
	border: none;
	border-radius: 0.5rem;
	font-size: 1rem;
	font-weight: bold;
	text-transform: uppercase;
	letter-spacing: 0.1rem;
	transition: background-color 0.3s ease-in-out;
  }
  
  .product-categories-card__button:hover {
	background-color: #444;
  }
  